*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  border: none;
  padding: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ul,
ol,
li {
  list-style: none;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  background-color: transparent;
  padding: 0;
  color: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
}

a,
a:link,
a:visited {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

img,
svg,
video,
canvas,
audio {
  display: block;
  vertical-align: middle;
  border: none;
  max-width: 100%;
  height: auto;
}

input,
textarea,
button,
select {
  box-sizing: border-box;
  margin: 0;
  border: none;
  background-color: transparent;
  padding: 0;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
  box-shadow: none;
  border: none;
  background: none;
  text-align: inherit;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  appearance: none;
}

:-moz-focusring {
  outline: none;
}

button,
input,
select,
textarea {
  background: none;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
}

legend {
  display: block;
  padding: 0;
}

label {
  cursor: pointer;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  margin: 0;
  border: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}

caption,
th,
td {
  vertical-align: top;
  padding: 0;
  font-weight: normal;
  text-align: left;
}

th {
  font-weight: bold;
}

input,
textarea,
select,
button {
  font: inherit;
}

address {
  font-style: normal;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

figure {
  margin: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
}

iframe {
  border: none;
}

template {
  display: none;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

:focus {
  outline: none;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  outline: none;
  box-shadow: none;
  border: none;
  background-color: transparent;
  padding: 0;
  color: inherit;
}

