.links {
  margin-top: 32px;
  margin-inline: auto;
  max-width: 1400px;
  width: 95%;
}
.links-button {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  transition: background-color 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  box-shadow: 0 5px 5px 0 var(--shadow);
  border: 1px solid var(--links_border);
  border-radius: 10px;
  background-color: var(--links_background);
  padding-inline: 15px;
  padding-block: 10px;
  height: 100%;
}
.links-button:hover {
  z-index: 15;
  cursor: pointer;
  background-color: var(--links_hover);
}
.links-button .name_container {
  display: flex;
  align-items: center;
  max-width: fit-content;
  max-height: fit-content;
}
.links-button .name_container .icon {
  flex-shrink: 0;
  vertical-align: middle;
  margin-inline-end: 8px;
  width: 24px;
  height: 24px;
  color: var(--accent);
}
.links-button .name_container .icon path {
  margin: auto;
}
.links-button .name_container .name {
  color: var(--accent);
  font-weight: 700;
  font-size: 18px;
  font-family: "Mulish", sans-serif;
}
.links-button .description {
  opacity: 80%;
  margin-block-start: 5px;
  font-size: 14px;
}
.links-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 10px;
}

.faq-container {
  margin-inline: auto;
  max-width: 1000px;
  width: 97.5%;
}
.faq-container-flex {
  display: flex;
  flex-direction: row;
  margin-inline: auto;
  max-width: 1400px;
}
.faq-title {
  margin-inline: 10px;
}
.faq-title h1 {
  display: none;
}
.faq-summary {
  display: flex;
  position: relative;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  filter: drop-shadow(0 3px 5px var(--shadow));
  background-image: linear-gradient(to bottom, var(--summary_background), transparent);
  transition: background-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  cursor: pointer;
  margin-block: 10px;
  border-top: solid 1px var(--summary_border);
  border-right: solid 1px var(--summary_border);
  border-bottom: solid 4px var(--summary_border);
  border-left: solid 1px var(--summary_border);
  border-radius: 10px;
  background-color: var(--summary_background);
  padding: 10px 15px;
  color: var(--summary_text);
  font-weight: 800;
  font-size: 20px;
  line-height: 1.25;
  font-family: "Mulish", sans-serif;
  text-transform: uppercase;
}
.faq-summary-left {
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.faq-summary .copy_button {
  fill: var(--accent);
}
.faq-summary:hover {
  background-color: var(--links_hover);
}
.faq-section {
  margin-block: 15px;
  border: solid 1px var(--content-background-border);
  border-radius: 10px;
  background-color: var(--content-background-color);
}
.faq-content {
  border-radius: 10px;
  line-height: 1.75;
}
.faq-content h2 {
  margin-bottom: -10px;
  margin-inline: 20px;
  padding-top: 10px;
  color: var(--summary_text);
  font-weight: 800;
  font-size: 22px;
  font-family: "Mulish", sans-serif;
  text-transform: uppercase;
}
.faq-content p {
  margin-inline: 20px;
  margin-block: 15px;
}
@media screen and (max-width: 375px) {
  .faq-content {
    hyphens: auto;
  }
}
.faq-content .message-correct, .faq-content .message-incorrect {
  max-width: 450px;
}
.faq-content .message-flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 25px;
}
.faq-content .message-left {
  position: relative;
  top: 0;
  left: 0;
  filter: saturate(0%);
  margin-right: auto;
  margin-left: 10px;
  margin-block: 10px;
  border: 1px solid var(--mark_border);
  border-radius: 10px;
  background-color: var(--mark_background);
  padding: 10px;
  max-width: 70%;
  color: var(--mark_text);
  font-size: 14px;
  line-height: 1.15;
  text-align: left;
}
.faq-content .message-right {
  filter: saturate(100%);
  margin-right: 10px;
  margin-left: auto;
  margin-block: 10px;
  border: 1px solid var(--mark_border);
  border-radius: 10px;
  background-color: var(--mark_background);
  padding: 10px;
  max-width: 70%;
  color: var(--mark_text);
  font-size: 14px;
  line-height: 1.15;
  text-align: right;
}
.faq-content a {
  color: var(--accent);
  font-weight: 700;
  text-decoration: underline;
}
.faq-content li {
  position: relative;
  padding-inline: 15px;
  padding-inline-start: 30px;
  padding-block: 10px;
  list-style-type: none;
}
.faq-content li:not(:last-child) {
  border-bottom: 1px solid var(--content-background-border);
}
.faq-content li::before {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  opacity: 50%;
  border-radius: 10px;
  background-color: var(--accent);
  width: 3px;
  height: 65%;
  content: "";
}
.faq-content code {
  display: block;
  margin-block: 10px;
  border-radius: 10px;
  background-color: #303030;
  padding: 10px 15px;
  color: #ffffff;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.25;
  font-family: "JetBrains Mono", monospace;
  user-select: text;
}
.faq-content mark {
  filter: saturate(75%);
  border: 1px solid var(--mark_border);
  border-radius: 5px;
  background-color: var(--mark_background);
  padding: 2px 3px;
  color: var(--mark_text);
}
.flexible-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  padding-inline: 10px;
  margin: 0 auto;
  max-width: 800px;
}
.flexible-links button,
.flexible-links a {
  margin-block: 0;
  font-weight: 700;
  color: var(--accent);
  border: 2px solid var(--button_border);
  border-radius: 10px;
  background-color: var(--button_background);
  padding-inline: 10px;
  padding-block: 5px;
  font-size: 16px;
  text-decoration: none;
}

.addition-info, .addition-warning, .addition-danger {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
  margin-block: 10px;
  border-radius: 10px;
  padding: 10px 15px;
  font-size: 15px;
  line-height: 1.5;
}
.addition-info:first-child, .addition-warning:first-child, .addition-danger:first-child {
  margin-top: 0;
}
.addition-info:last-child, .addition-warning:last-child, .addition-danger:last-child {
  margin-bottom: 0;
}
.addition-info a, .addition-warning a, .addition-danger a {
  font-weight: 700;
  text-decoration: underline;
}
.addition-info mark, .addition-warning mark, .addition-danger mark {
  filter: saturate(125%);
  font-style: italic;
}
.addition-info li, .addition-warning li, .addition-danger li {
  margin-inline-start: 20px;
  margin-block: 5px;
  padding-inline-start: 2px;
  padding-block: 0;
  list-style-type: decimal;
}
.addition-info li:not(:last-child), .addition-warning li:not(:last-child), .addition-danger li:not(:last-child) {
  border-bottom: none;
}
.addition-info li::before, .addition-warning li::before, .addition-danger li::before {
  display: none;
}
.addition-info {
  border: 1px solid var(--addition_info-border);
  background-color: var(--addition_info);
  color: var(--addition_info-text);
}
.addition-info .change_os {
  margin-inline: 5px;
  border: 1px solid var(--addition_info-border);
  border-radius: 5px;
  background-color: var(--addition_info-mark);
  padding: 5px;
  color: var(--addition_info-text);
  line-height: 1.25;
  text-align: center;
}
.addition-info a {
  color: var(--addition_info-text);
}
.addition-info mark {
  border: 1px solid var(--addition_info-border);
  background-color: var(--addition_info-mark);
  color: var(--addition_info-text);
}
.addition-warning {
  border: 1px solid var(--addition_warning-border);
  background-color: var(--addition_warning);
  color: var(--addition_warning-text);
}
.addition-warning a {
  color: var(--addition_warning-text);
}
.addition-warning mark {
  border: 1px solid var(--addition_warning-border);
