video {
  border: none;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  max-height: 65vh;
}

iframe {
  border: none;
  border-radius: 10px;
  width: 100%;
  height: 56.25vw;
  max-height: 500px;
}

img {
  border: none;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  max-height: 65vh;
}

.header {
  display: flex;
  position: fixed;
  top: 0;
  left: 50%;
  justify-content: space-between;
  align-items: center;
  transform: translateX(-50%);
  z-index: 100;
  transition: background-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1), box-shadow 0.5s cubic-bezier(0.075, 0.82, 0.165, 1), border 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  margin-top: 10px;
  border-radius: 15px;
  background-color: var(--header_background);
  padding-inline: 15px;
  padding-block: 10px;
  width: 95%;
  max-width: 1200px;
}
.header svg {
  fill: var(--accent);
  width: 20px;
  height: 20px;
}
.header-left, .header-right {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  transition: background-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1), box-shadow 0.5s cubic-bezier(0.075, 0.82, 0.165, 1), opacity 0.5s cubic-bezier(0.075, 0.82, 0.165, 1), visibility 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  max-height: max-content;
}
.header-left .icon, .header-right .icon {
