  color: var(--accent);
  font-style: italic;
  font-weight: 800;
  font-size: clamp(16px, 12vw, 20px);
  font-family: "JetBrains Mono", monospace;
}
.header-right {
  gap: 15px;
}

table {
  margin: 0 auto;
  border-radius: 10px;
