  max-width: 1000px;
  font-size: clamp(13px, 1.5vw, 15px);
}

@media (max-width: 350px) {
  table {
    word-wrap: break-word;
    word-break: break-all;
  }
  table td,
  table th {
    padding: 5px 3px;
  }
}
td,
th {
  vertical-align: middle;
  border: 1px solid var(--table_background-border);
  padding: 5px 10px;
  text-align: center;
}

td:first-child,
th:first-child {
  border-left: none;
}

td:last-child,
th:last-child {
  border-right: none;
}

tr:first-child th {
  border-top: none;
}

tr:last-child td,
tr:last-child th {
  border-bottom: none;
}

:root {
  --accent-hue: 210;
  --header_shadow: hsla(0, 0%, 0%, 0.05);
  --shadow: hsla(0, 0%, 0%, 0.1);
}

.light {
  --background-color: hsl(0, 0%, 92.5%);
  --text-color: hsl(0, 0%, 20%);
  --content-background-color: hsl(0, 0%, 100%);
  --content-background-border: hsl(0, 0%, 95%);
  --header_background: hsla(0, 0%, 100%, 0.5);
  --sber-background: hsl(148, 75%, 97%);
  --sber-text: hsl(148, 50%, 25%);
  --sber-hover: hsl(148, 50%, 85%);
  --sber-border: hsl(148, 90%, 85%);
  --yoomoney-background: hsl(257, 75%, 97%);
  --yoomoney-text: hsl(257, 50%, 25%);
  --yoomoney-hover: hsl(257, 50%, 85%);
  --yoomoney-border: hsl(257, 90%, 85%);
  --accent: hsl(var(--accent-hue), 65%, 45%);
  --mark_background: hsl(var(--accent-hue), 75%, 97%);
  --mark_text: hsl(var(--accent-hue), 50%, 25%);
  --mark_border: hsl(var(--accent-hue), 90%, 85%);
  --button_background: hsl(var(--accent-hue), 50%, 95%);
  --button_border: hsl(var(--accent-hue), 50%, 85%);
  --header_border: hsla(var(--accent-hue), 35%, 80%, 0.5);
  --summary_background: hsl(var(--accent-hue), 41%, 97%);
  --summary_border: hsl(var(--accent-hue), 35%, 80%);
  --summary_text: var(--accent);
  --links_background: hsl(var(--accent-hue), 35%, 97.5%);
  --links_border: hsl(var(--accent-hue), 75%, 75%);
  --links_hover: hsl(var(--accent-hue), 50%, 90%);
  --addition_info: hsl(200, 100%, 85%);
  --addition_info-border: hsl(200, 90%, 75%);
  --addition_info-text: hsl(200, 75%, 25%);
  --addition_info-mark: hsl(200, 70%, 75%);
  --addition_warning: hsl(50, 100%, 85%);
  --addition_warning-border: hsl(50, 90%, 50%);
  --addition_warning-text: hsl(50, 75%, 20%);
  --addition_warning-mark: hsl(50, 70%, 75%);
  --addition_danger: hsl(0, 100%, 90%);
  --addition_danger-border: hsl(0, 80%, 75%);
  --addition_danger-text: hsl(0, 75%, 25%);
  --addition_danger-mark: hsl(0, 70%, 80%);
  --table_background: hsl(0, 0%, 95%);
  --table_background-border: hsl(0, 0%, 90%);
}

.dark {
  --background-color: hsl(0, 0%, 0%);
  --text-color: hsl(0, 0%, 100%);
  --content-background-color: hsl(0, 0%, 7.5%);
  --content-background-border: hsl(0, 0%, 15%);
  --header_background: hsla(0, 0%, 7.5%, 0.75);
  --accent: hsl(var(--accent-hue), 100%, 70%);
  --mark_background: hsl(var(--accent-hue), 25%, 27%);
  --mark_border: hsl(var(--accent-hue), 90%, 85%);
  --mark_text: hsl(var(--accent-hue), 72%, 90%);
  --button_background: hsl(var(--accent-hue), 25%, 12.5%);
  --button_border: hsl(var(--accent-hue), 35%, 25%);
  --header_border: hsla(var(--accent-hue), 30%, 30%, 0.5);
  --summary_background: hsl(var(--accent-hue), 25%, 8%);
  --summary_border: hsl(var(--accent-hue), 35%, 25%);
  --summary_text: var(--accent);
  --links_background: hsl(var(--accent-hue), 35%, 7.5%);
  --links_border: hsl(var(--accent-hue), 75%, 20%);
  --links_hover: hsl(var(--accent-hue), 50%, 15%);
  --addition_info: hsl(200, 70%, 15%);
  --addition_info-border: hsl(200, 60%, 25%);
  --addition_info-text: hsl(200, 75%, 85%);
  --addition_info-mark: hsl(200, 90%, 20%);
  --addition_warning: hsl(50, 70%, 25%);
  --addition_warning-border: hsl(50, 90%, 35%);
  --addition_warning-text: hsl(50, 75%, 85%);
  --addition_warning-mark: hsl(50, 90%, 30%);
  --addition_danger: hsl(0, 50%, 25%);
  --addition_danger-border: hsl(0, 70%, 35%);
  --addition_danger-text: hsl(0, 100%, 85%);
  --addition_danger-mark: hsl(0, 50%, 30%);
  --table_background: hsl(0, 0%, 12.5%);
  --table_background-border: hsl(0, 0%, 20%);
}

.theme-modal .title {
  font-weight: 700;
  font-size: 24px;
  font-family: "Mulish", sans-serif;
}
.theme-button {
  padding: 10px;
}
.theme-button svg {
  fill: var(--text-color);
}
.theme-button-selected {
  border-radius: 1000px;
  background-color: var(--accent);
}
.theme-button-selected svg {
  fill: #fff;
}

.search-input {
  padding-inline-start: 16px;
  padding-block: 8px;
  width: 100%;
}
.search-input-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 20px;
  border: 1px solid var(--content-background-border);
  border-radius: 10px;
  padding-inline-end: 16px;
  width: 100%;
}
.search-results {
  max-height: 70vh;
  overflow-y: auto;
  scrollbar-width: none;
}
.search-results::before {
  position: absolute;
  bottom: 20px;
  left: 0;
  z-index: 5;
  background: linear-gradient(to bottom, transparent, var(--content-background-color));
  width: 100%;
  height: 15%;
  pointer-events: none;
  content: "";
}
.search-link {
  display: block;
  border-radius: 10px;
}
.search-link:last-child {
  border-bottom: none;
}
.search-title {
  transition: background-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  cursor: pointer;
  margin-block: 10px;
  border-top: solid 1px var(--summary_border);
  border-right: solid 1px var(--summary_border);
  border-bottom: solid 4px var(--summary_border);
  border-left: solid 1px var(--summary_border);
  border-radius: 10px;
  background-color: var(--summary_background);
  padding: 10px 15px;
  color: var(--summary_text);
  font-weight: 800;
  font-size: 20px;
  line-height: 1.25;
  font-family: "Mulish", sans-serif;
  text-transform: uppercase;
}
.search-title mark {
  filter: saturate(75%);
  border: 1px solid var(--mark_border);
  border-radius: 5px;
  background-color: var(--mark_background);
  padding: 0 1px;
  color: var(--mark_text);
}
.search-title:hover {
  background-color: var(--links_hover);
}
.search-content {
  line-clamp: 3;
  padding: 10px;
  overflow: hidden;
  font-size: 16px;
}
.search-content mark {
  filter: saturate(75%);
  border: 1px solid var(--mark_border);
  border-radius: 5px;
  background-color: var(--mark_background);
  padding: 0 1px;
  color: var(--mark_text);
}

.support {
  display: flex;
  justify-content: flex-end;
}
.support-modal li {
  position: relative;
  padding-inline: 15px;
  padding-inline-start: 30px;
  padding-block: 10px;
  list-style-type: none;
}
.support-modal li:not(:last-child) {
  border-bottom: 1px solid var(--content-background-border);
}
.support-modal li::before {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  opacity: 50%;
  border-radius: 10px;
  background-color: var(--accent);
  width: 3px;
  height: 65%;
  content: "";
}
.support-button {
  border-radius: 10px;
  padding: 10px;
  font-size: 13px;
}
.support-button-donut {
  border: 1px solid hsl(45, 90%, 75%);
  border-radius: 10px;
  background-color: hsl(45, 75%, 90%);
  padding-inline: 10px;
  padding-block: 5px;
  color: black;
  font-weight: 700;
}
.support-button-share {
  color: var(--accent);
}

.copy-cardnumber {
  display: block;
  filter: saturate(75%);
  transition: background-color 0.5s cubic-bezier(0.075, 0.82, 0.165, 1), filter 0.35s cubic-bezier(0.075, 0.82, 0.165, 1);
  margin-block: 10px;
  border-radius: 5px;
  padding: 5px 10px;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
}
.copy-cardnumber:active {
  filter: brightness(0.85);
}
.copy-cardnumber-sber {
  border: solid 1px var(--sber-border);
  background-color: var(--sber-background);
  color: var(--sber-text);
  text-align: center;
}
.copy-cardnumber-sber:hover {
  background-color: var(--sber-hover);
}
.copy-cardnumber-yoomoney {
  border: solid 1px var(--yoomoney-border);
  background-color: var(--yoomoney-background);
  color: var(--yoomoney-text);
  text-align: center;
}
.copy-cardnumber-yoomoney:hover {
  background-color: var(--yoomoney-hover);
}

html,
body {
  scroll-padding-top: 80px;
  user-select: none;
  padding: 0;
  margin: 0;
  color: var(--text-color);
  background: var(--background-color);
  font-family: "Inter", sans-serif;
  font-weight: 350;
}

html {
  scrollbar-width: none;
}

.main {
  margin-top: 64px;
  overflow: hidden;
}

.footer {
  opacity: 0.5;
  margin-block: 30px;
  margin-inline: auto;
  padding-inline: 10px;
  font-size: 12px;
  text-align: center;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

.visible {
  opacity: 1;
  visibility: visible;
}

.visual-hidden {
  display: none;
}

.no-javascript,
.not-found {
  color: var(--text-color);
  background-color: var(--background-color);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  line-height: 1.5;
}
.no-javascript-container,
.not-found-container {
  display: inline-flex;
  flex-direction: column;
  gap: 10px;
  max-width: 400px;
  background-color: var(--content-background-color);
