  border-radius: 15px;
  box-shadow: 0 10px 25px 10px rgba(0, 0, 0, 0.15);
}
.no-javascript-title,
.not-found-title {
  color: var(--accent);
  font-family: "Mulish", sans-serif;
  font-weight: 800;
  text-align: left;
  font-size: 32px;
  text-transform: uppercase;
  hyphens: auto;
