  font-size: 12px;
  margin-inline: 5px;
  opacity: 0.5;
}
.no-javascript-button,
.not-found-button {
  font-weight: 500;
  border-radius: 10px;
  display: inline-flex;
  gap: 10px;
  padding: 10px 15px;
